import { useState, useEffect } from 'react';

export default function MovieList() {

    const [data, getData] = useState(false);

    useEffect(() => {
        fetch('https://api.themoviedb.org/3/trending/movie/week?api_key=8c20094b9d32bd14049b323d7d8294d0')
            .then(response => response.json())
            .then(result => getData(result))
    }, []);

    console.log(data);
    console.log(data.results);

    return (
        <ul>
            {data.results.map((item, i) => 
            <div key={i} className="card">
                <img className="card-img-top" src={"http://image.tmdb.org/t/p/w500/" + item.backdrop_path} alt="" />
                <div className="card-body">
                    <h5 className="card-title">{item.original_title}</h5>
                    <p className="card-text">{item.overview}</p>
                </div>
            </div>)}
        </ul>
    )
}

